@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap');

/* Reset */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Montserrat', sans-serif;
  background: linear-gradient(135deg, #89f7fe 0%, #66a6ff 100%);
  color: #1a1a2e;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

header {
  background: rgba(26, 26, 46, 0.9);
  padding: 2rem 1rem;
  color: #fff;
  text-align: center;
  box-shadow: 0 4px 15px rgba(0,0,0,0.2);
}

header h1 {
  font-size: 3rem;
  letter-spacing: 4px;
  margin-bottom: 0.3rem;
}

header p {
  font-size: 1.25rem;
  font-weight: 300;
  letter-spacing: 1.5px;
  margin-top: 0;
  opacity: 0.85;
}

main {
  flex: 1;
  padding: 3rem 1.5rem;
  max-width: 900px;
  margin: 0 auto;
  background: #ffffffdd;
  border-radius: 20px;
  box-shadow: 0 8px 30px rgba(0,0,0,0.12);
  margin-top: -3rem;
  backdrop-filter: saturate(180%) blur(12px);
  position: relative;
}

section {
  margin-bottom: 3.5rem;
}

.section-title {
  font-size: 2.2rem;
  margin-bottom: 1rem;
  color: #0b1140;
  border-bottom: 3px solid #556cd6;
  padding-bottom: 0.3rem;
  display: flex;
  align-items: center;
  gap: 1rem;
}

.section-icon {
  height: 36px;
  border-radius: 6px;
  box-shadow: 0 2px 8px rgb(0 0 0 / 0.15);
}

.section-text {
  font-size: 1.15rem;
  line-height: 1.6;
  color: #333751;
  margin-top: 1rem;
}

.features-list {
  list-style: none;
  padding-left: 1.2rem;
  margin-top: 1rem;
  color: #1a1a2e;
}

.features-list li {
  margin-bottom: 1rem;
  font-size: 1.1rem;
  position: relative;
  padding-left: 1.5rem;
}

.features-list li::before {
  content: "âœ“";
  color: #556cd6;
  position: absolute;
  left: 0;
  font-weight: bold;
}

.section-image {
  margin-top: 1rem;
  width: 100%;
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.15);
  max-height: 280px;
  object-fit: cover;
  user-select: none;
}

footer {
  text-align: center;
  padding: 1rem 1rem 2rem;
  color: #eef0f7;
  font-size: 0.9rem;
  user-select: none;
}

/* Chat launch button */
.chat-launch-btn {
  position: fixed;
  bottom: 30px;
  right: 30px;
  background-color: #556cd6;
  color: white;
  border: none;
  border-radius: 50px;
  padding: 15px 25px;
  font-size: 1rem;
  font-weight: 700;
  box-shadow: 0 6px 15px rgba(85,108,214,0.4);
  cursor: pointer;
  transition: background-color 0.3s ease;
  z-index: 1000;
}

.chat-launch-btn:hover {
  background-color: #3a4abf;
}

/* Chat modal overlay */
.chat-modal {
  position: fixed;
  bottom: 85px;
  right: 30px;
  width: 350px;
  max-height: 500px;
  background: white;
  border-radius: 15px;
  box-shadow: 0 15px 40px rgba(0,0,0,0.3);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  font-family: 'Nunito', sans-serif;
  z-index: 1100;
}

.chat-header {
  background-color: #556cd6;
  color: white;
  padding: 1rem;
  font-weight: 700;
  font-size: 1.25rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.chat-close-btn {
  background: transparent;
  border: none;
  color: white;
  font-size: 1.3rem;
  cursor: pointer;
  font-weight: 700;
}

.chat-messages {
  flex: 1;
  padding: 1rem;
  overflow-y: auto;
  background: #f7f8ff;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.chat-message {
  max-width: 75%;
  padding: 10px 15px;
  border-radius: 20px;
  font-size: 0.9rem;
  line-height: 1.3;
  word-wrap: break-word;
  box-shadow: 0 2px 7px rgb(0 0 0 / 0.1);
}

.chat-message.user {
  background-color: #556cd6;
  color: white;
  align-self: flex-end;
  border-bottom-right-radius: 4px;
}

.chat-message.bot {
  background-color: #e1e4ff;
  color: #333;
  align-self: flex-start;
  border-bottom-left-radius: 4px;
}

.chat-input-area {
  background: #fff;
  padding: 10px 15px;
  border-top: 1px solid #d2d6ff;
  display: flex;
  gap: 10px;
}

.chat-input-area input[type="text"] {
  flex: 1;
  padding: 0.5rem 1rem;
  border-radius: 25px;
  border: 1.5px solid #c0c7ff;
  font-size: 1rem;
  outline: none;
  transition: border-color 0.3s ease;
}

.chat-input-area input[type="text"]:focus {
  border-color: #556cd6;
  box-shadow: 0 0 6px #556cd6aa;
}

.chat-input-area button {
  background-color: #556cd6;
  color: white;
  border: none;
  padding: 0 20px;
  border-radius: 25px;
  font-weight: 700;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}

.chat-input-area button:disabled {
  background-color: #a5a9d6;
  cursor: not-allowed;
}

.chat-input-area button:hover:not(:disabled) {
  background-color: #3a4abf;
}

/* Responsive chat modal */
@media (max-width: 400px) {
  .chat-modal {
    right: 10px;
    bottom: 70px;
    width: 90vw;
    max-height: 60vh;
  }
}

/* Responsive main */
@media (max-width: 600px) {
  header h1 {
    font-size: 2.2rem;
  }
  main {
    padding: 2rem 1rem;
    margin-top: -2rem;
    border-radius: 15px;
  }
  .section-title {
    font-size: 1.6rem;
  }
  .section-image {
    max-height: 180px;
  }
}
.center-screen-container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, #89f7fe 0%, #66a6ff 100%);
}

.chat-launch-btn.main-focus {
  position: static;
  padding: 25px 50px;
  font-size: 1.5rem;
  border-radius: 40px;
  box-shadow: 0 8px 25px rgba(85, 108, 214, 0.6);
}
.chat-button-container {
  display: flex;
  justify-content: center;
  margin: 2rem 0;
}

.chat-launch-btn.main-focus {
  padding: 18px 40px;
  font-size: 1.4rem;
  border-radius: 45px;
  box-shadow: 0 8px 30px rgba(85,108,214,0.5);
  background-color: #556cd6;
  color: white;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.chat-launch-btn.main-focus:hover {
  background-color: #3a4abf;
}
